import React from "react";
import { Box, Grid, Container, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useHomeBannerStyle = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
  //Banner
  bannerBg: {
    minHeight: "100vh",
    backgroundColor: theme.palette.common.homeBannaerBg,
    [theme.breakpoints.down("xs")]: {
      minHeight: "auto",
    },

    [theme.breakpoints.down("xs")]: {
      "& .MuiContainer-root": {
        "& .MuiGrid-align-items-xs-center": {
          alignItems: "flex-end",
        },
      },
    },

    "& .flagDiv": {
      position: "absolute",
      top: "30px",
      right: "13px",
      backgroundImage: "url('/static/images/location.gif')",
      backgroundRepeat: "no-repeat",
      backgroundPosition: "right",
      paddingRight: theme.spacing(5),
      color: theme.palette.common.black,
      fontWeight: 600,
      height: 40,
      paddingTop: 8,
      [theme.breakpoints.down("xs")]: {
        top: "12px",
      },
    },

    "& h1": {
      fontSize: "2rem",
      paddingRight: theme.spacing(4.5),
      lineHeight: "3.5rem",
      [theme.breakpoints.down("xs")]: {
        fontSize: "1.275rem",
        textAlign: "center",
        paddingRight: theme.spacing(0),
        marginTop: theme.spacing(4.5),
        lineHeight: "2.3rem",
        fontWeight: "bold",
      },
      "& span": {
        fontSize: "2.125rem",
        color: theme.palette.primary.main,
        fontWeight: 700,
        [theme.breakpoints.down("xs")]: {
          fontSize: "1.275rem",
          textAlign: "center",
        },
      },
    },
    "& .MuiTypography-h4": {
      marginTop: theme.spacing(8),
      color: theme.palette.text.secondary,

      "& b": {
        color: theme.palette.common.black,
        fontWeight: "bold",
      },
      [theme.breakpoints.down("xs")]: {
        fontSize: "0.9rem",
        marginTop: theme.spacing(4),
      },
    },
    "& .MuiTypography-colorSecondary": {
      fontSize: "1.8rem",
    },
    "& .desktopList": {
      paddingLeft: theme.spacing(0),
      paddingBottom: theme.spacing(1.25),
      display: "inline-flex",
      width: "105%",
      marginLeft: "0%",
      paddingTop: "3%",
      [theme.breakpoints.down("xs")]: {
        display: "inline-block",
        marginLeft: "0% !important",
        paddingTop: "0%",
      },
      "& li": {
        listStyleType: "none",
        width: "100%",
        textAlign: "left",
        color: theme.palette.common.black,
        fontWeight: "bold",
        marginRight: "3%",
        borderRight: `1px solid ${theme.palette.primary.main}`,
        paddingLeft: "4.5%",
        backgroundImage: "url('/static/images/green-tick.svg')",
        backgroundRepeat: "no-repeat",
        backgroundPosition: "0% 15%",
        "&:first-child": {
          width: "75%",
        },
        "&:last-child": {
          borderRight: `0px solid ${theme.palette.primary.main}`,
        },

        [theme.breakpoints.down("xs")]: {
          borderRight: `0px solid ${theme.palette.primary.main}`,
          textAlign: "left",
          color: theme.palette.text.secondary,
          paddingLeft: "0%",
          marginRight: "0%",
          marginBottom: 6,
        },
        "& .MuiSvgIcon-root": {
          display: "none",
          [theme.breakpoints.down("xs")]: {
            display: "inline-block",
            width: "0.8em",
            verticalAlign: "bottom",
            color: "transparent",
            marginRight: theme.spacing(0.5),
          },
        },

        "& span": {
          color: theme.palette.primary.main,
          fontWeight: "bolder",
          display: "block",
          [theme.breakpoints.down("xs")]: {
            display: "inline-block",
            color: theme.palette.primary.main,
          },
        },
      },
    },
    "& .mobileList": {
      paddingLeft: theme.spacing(1.25),
      paddingBottom: theme.spacing(1.25),
      "& li": {
        listStyleType: "none",
        [theme.breakpoints.up("xs")]: {
          textIndent: "-22px",
          paddingLeft: "12px",
        },
        "& .MuiSvgIcon-root": {
          width: "0.8em",
          verticalAlign: "bottom",
          color: theme.palette.primary.main,
        },
        "& span": {
          color: theme.palette.secondary.main,
          fontWeight: "bold",
        },
      },
    },
  },
  bannerRow: {
    position: "relative",
    minHeight: "100vh",
    backgroundImage: "url('/construction.svg')",
    backgroundRepeat: "no-repeat",
    backgroundSize: "48%",
    backgroundPosition: "right",

    [theme.breakpoints.down("xs")]: {
      backgroundPosition: "center",
    },
  },
}));

export default function UnderConstruction() {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const classes = useHomeBannerStyle();

  return (
    <Box className={classes.bannerBg}>
      <Container>
        <Grid
          container
          spacing={3}
          alignItems="center"
          direction="row"
          className={classes.bannerRow}
        >
          <Grid item xs={12} sm={12} md={7} lg={7}>
            <Typography variant="h1" component="h1">
              Sorry, we're under <span> {"construction"}</span> !
            </Typography>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
}
